import { createApp } from 'vue';
import App from './App.vue';
import router from './router';
import axios from 'axios';

// Axios Í∏∞Î≥∏ ÏÑ§Ï†ï
axios.defaults.withCredentials = true; // withCredentials ÏÑ§Ï†ï Ï∂îÍ∞Ä
axios.defaults.xsrfCookieName = 'csrftoken';
axios.defaults.xsrfHeaderName = 'X-CSRFToken';

// API Í∏∞Î≥∏ URL ÏÑ§Ï†ï
const apiBaseUrl = process.env.VUE_APP_API_BASE_URL || 'http://localhost:8000';

console.log(`API Base URL: ${apiBaseUrl}`); // ÎîîÎ≤ÑÍπÖÏö© Î°úÍ∑∏

// CSRF ÌÜ†ÌÅ∞ÏùÑ Í∞ÄÏ†∏ÏôÄ AxiosÏóê ÏÑ§Ï†ï
axios.get(`${apiBaseUrl}/map/set-csrf-token/`)
    .then(response => {
        console.log('main.js >> CSRF Token response:', response); // ÎîîÎ≤ÑÍπÖÏö© Î°úÍ∑∏
        axios.defaults.headers.common['X-CSRFToken'] = response.data.csrfToken;
    })
    .catch(error => {
        console.error('Error getting CSRF Token: ', error); // ÎîîÎ≤ÑÍπÖÏö© Î°úÍ∑∏
    });

// AxiosÎ•º Vue Ïù∏Ïä§ÌÑ¥Ïä§Ïóê ÌîåÎü¨Í∑∏Ïù∏ÏúºÎ°ú Ï∂îÍ∞Ä
const app = createApp(App);
app.config.globalProperties.$http = axios;

app.use(router).mount('#app');